import React, { Component } from 'react';
import { connect } from "react-redux";
import { dellDay,addDay } from "../redux/actions_calendar";

class Day extends Component {
    constructor(props) {
        super(props);
        this.state = {
            hover_style:{},
            class_day_number:"disabled_day_number",
            class_days_number : {active:"active_day_number",disabled:"disabled_day_number"},
            class_day:"disabled_day",
            class_days : {active:"active_day",disabled:"disabled_day"},
            active:false };
        
    }
    
    isActive = () => {
        
        if(this.props.lcm === true){
            if(this.state.active === false){
                this.setState({ class_day : this.state.class_days.active })
                this.setState({ class_day_number : this.state.class_days_number.active })
                this.setState({ active:true })
                this.props.addDay(this.props.mount, this.props.value)
            }
            else{
                this.setState({ class_day : this.state.class_days.disabled })
                this.setState({ class_day_number : this.state.class_days_number.disabled })
                this.setState({ active:false })
                this.props.dellDay(this.props.mount, this.props.value)
            }
        }
    }
    isClick = () =>{
        if(this.state.active === false){
            this.setState({ class_day : this.state.class_days.active })
            this.setState({ class_day_number : this.state.class_days_number.active })
            this.setState({ active:true })
            this.props.addDay(this.props.mount, this.props.value)
        }
        else{
            this.setState({ class_day : this.state.class_days.disabled })
            this.setState({ class_day_number : this.state.class_days_number.disabled })
            this.setState({ active:false })
            this.props.dellDay(this.props.mount, this.props.value)
        }
    }

    render() {
        return(
            <div>
            <div   className ={"day "+this.state.class_day} style={this.state.hover_style}>
                <div className ={"day_number "+this.state.class_day_number}>
                    {this.props.value}
                </div>
            </div>
            <div className ="click_day" onMouseDown={() => this.isClick()} onMouseOver={() => this.isActive() }></div>
            </div>
        )
    }
}

export default connect(null, {dellDay,addDay})(Day);

